<template>
    <PatchMeta :title="contentCard.title" :description="contentCard.description" :previewUrl="previewUrl" />
    <div class="m-2 text-right">
      <button v-if="isLoggedIn" type="button" class="btn btn-light border m-1" @click="signOut">Close Session  <i class="fa fa-sign-out"></i></button>
      <button v-if="!isLoggedIn" type="button" class="btn btn-light border m-1" @click="signIn">Sign In  <i class="fa fa-sign-in"></i></button>
    </div>

    <div class="container p-3" v-bind:style="`background-color: ${userData.bgColor}; color: ${invertedHex};`">

        <div class="row align-items-center p-2" v-if="isOwnProfile">
          <h4><i class="fa fa-pencil mr-1"></i></h4>
          <label for="editPostModeCheckbox" class="el-switch"><input type="checkbox" name="switch" v-model="editMode" id="editPostModeCheckbox">
            <span class="el-switch-style"></span>
          </label>
        </div>

        <div v-if="editMode || contentCard.title" style="margin: 15px;">
            <h1>
                <a v-if="!editMode" v-bind:style="`color: ${invertedHex};`" target="_blank" :href="contentCard.sourceUrl" class="post-title hidden-sm center"><EditableTextInput :field="'title'" :value="contentCard.title" :editMode="editMode" :postUid="postUid" /></a>
                <EditableTextInput v-else :field="'title'" v-bind:style="`color: ${invertedHex};`" :value="contentCard.title" :editMode="editMode" :postUid="postUid" class="post-title hidden-sm center" />
            </h1>
        </div>

        <hr v-bind:style="`background-color: ${invertedHex};`" class="mb-5 mt-3" />

        <div class="row">

            <div class="col-sm-4">
                <a v-show="!editMode" class="row" :href="`/#/u/${userUid}`" v-bind:style="`color: ${invertedHex}; text-decoration: none;`">
                    <div class="col-3 post-profile-img">
                        <img :src="userData.photoURL" v-bind:style="`border: solid 2px; ${invertedHex};`" />
                    </div>
                    <div class="pl-1 col-9">
                        <h4>{{userData.displayName}}</h4>
                    </div>
                </a>
                <div v-show="editMode" class="row" v-bind:style="`color: ${invertedHex};`">
                    <div class="col-3 post-profile-img">
                        <img :src="userData.photoURL" v-bind:style="`border: solid 2px; ${invertedHex};`" />
                    </div>
                    <div class="pl-1 col-9">
                        <h4>{{userData.displayName}}</h4>
                    </div>
                </div>

                <div class="mt-4 text-justify">
                    <EditableTextInput :field="'description'" :value="contentCard.description" :editMode="editMode" :postUid="postUid" class="justify" />
                </div>
            </div>

            <div class="col-sm-8">
                <ContentCard :cardBgColor="invertedHex" :contentCard="contentCard" :editMode="editMode" :userUid="userUid" :postUid="postUid" :isCardPreview="false" :onDelete="onDelete" />
            </div>

        </div>
    </div>
</template>

<script lang="ts">
import { auth, profilesCollection, getTimelineCollection } from '@/firebase.config'
import router from '@/router'
import { reactive, toRefs, computed, defineComponent } from 'vue'
import ContentCard from '@/components/shared/ContentCard.vue'
import EditableTextInput from '@/components/shared/EditableTextInput.vue'
import { UserProfile } from '../types/UserProfile'
import { invertHex } from '@/utils/utils'
import deleteContentCard from '@/services/deleteContentCard'
import PatchMeta from '@/components/shared/PatchMeta.vue'
import { BaseContentCard } from '@/types/BaseContentCard'

export default defineComponent({
  components: {
    ContentCard,
    EditableTextInput,
    PatchMeta
  },
  props: {
    userUid: String,
    postUid: String
  },
  async setup (props) {
    const signOut = () => auth.signOut()
    const signIn = () => router.push('/')
    const isLoggedIn = auth && auth?.currentUser
    const isOwnProfile = auth?.currentUser?.uid === props.userUid

    const postRef = getTimelineCollection(props.userUid || '').doc(props.postUid)
    const userRef = profilesCollection.doc(props.userUid)

    const [postDoc, userDoc] = await Promise.all([postRef.get(), userRef.get()])
    if (!postDoc?.exists || !userDoc?.exists) {
      console.log('Post or user not found, TODO 404')
      router.replace('/404')
      return
    }
    const postData: BaseContentCard = postDoc.data() as BaseContentCard
    const userData: UserProfile = userDoc.data() as UserProfile
    const reactiveData = reactive({
      isLoggedIn,
      isOwnProfile,
      contentCard: postData,
      userData,
      editMode: false
    })
    // Keep updating card template for own user profile
    if (isOwnProfile) {
      postRef.onSnapshot(async (postDoc) => {
        const data = postDoc.data() as BaseContentCard
        if (!data) return
        reactiveData.contentCard = postDoc.data() as BaseContentCard
      })
    }

    const previewUrl = postData?.previewVideo || postData?.previewImage || postData?.iFrameUrl

    // eslint-disable-next-line @typescript-eslint/no-use-before-define
    const invertedHex = computed(() => invertHex(userData.bgColor))

    const onDelete = async () => {
      if (!props.postUid) return
      if (await deleteContentCard(props.postUid, reactiveData?.contentCard?.hostedFileName)) {
        router.replace('/')
      }
    }

    return {
      ...toRefs(reactiveData),
      invertedHex,
      onDelete,
      previewUrl,
      signOut,
      signIn
    }
  }
})
</script>

<style lang="css">
/* Custom CSS here */

.post-content {
  min-height: 350px;
  max-height: 600px;
  object-fit: contain;
}

.post-profile-img img{
  object-fit: cover;
  border-radius: 50%;
  height: 60px;
  width: 60px;
}
</style>
