/* eslint-disable @typescript-eslint/no-explicit-any */

import axios from 'redaxios'
import cheerio from 'cheerio'
import { BaseContentCard, CardType } from '@/types/BaseContentCard'

const ABSOLUTE_URL_MATCHER = /^https?:\/\/[^\s/$.?#].[^\s]*$/

const resolveAbsoluteUrl = (baseUrl: string, relativeOrAbsolutePath: string): string => {
  if (ABSOLUTE_URL_MATCHER.test(relativeOrAbsolutePath)) return relativeOrAbsolutePath
  const { protocol, hostname: baseHost, pathname: basePathname } = new URL(baseUrl)
  const { hash, host, hostname, href, origin, password, pathname, port, search } = new URL(`${relativeOrAbsolutePath}`, `${protocol}//${baseHost}${basePathname}`)
  console.log({ hash, host, hostname, href, origin, password, pathname, port, protocol, search })
  return href
}

export const FetchUrlMeta = async (sourceUrl: string, knownType?: CardType, defaultTitle = '', defaultDescription = ''): Promise<BaseContentCard | void> => {
  try {
    if (!ABSOLUTE_URL_MATCHER.test(sourceUrl)) throw new Error('FetchUrlMeta: Invalid URL')

    let rawResponse
    try {
      rawResponse = await axios.get(sourceUrl, { maxRedirects: 0 })
    } catch ({ code, message }) {
      try {
        rawResponse = await axios.get(`https://cors-anywhere.herokuapp.com/${sourceUrl}`, { maxRedirects: 0 })
      } catch ({ code, message }) {
        rawResponse = ''
      }
    }

    const $ = cheerio.load(rawResponse?.data?.toString() || '')

    /*  */
    /* META */
    /*  */
    const ogTitle = $('meta[name="og:title"]')?.prop('content')?.trim()
    const rawTitle = $('title')?.text()?.trim()
    const title = `${ogTitle || rawTitle || defaultTitle}`
    const ogDescription = $('meta[name="og:description"]')?.prop('content')?.trim()
    const rawDescription = $('meta[name="description"]')?.prop('content')?.trim()
    const description = `${ogDescription || rawDescription || defaultDescription}`

    /*  */
    /* IMAGES */
    /*  */
    const rawImages: string[] = []
    const ogImage = $('meta[property="og:image"]')?.prop('content')
    if (ogImage) {
      rawImages.push(resolveAbsoluteUrl(sourceUrl, ogImage))
    }
    $('img').map((i: number, ele: any) => {
      const src = $(ele).prop('src')
      if (src) {
        rawImages.push(resolveAbsoluteUrl(sourceUrl, src))
      }
    })
    // sort images by URL length
    rawImages.sort((a, b) => a.length - b.length)
    const previewImage = rawImages.length ? rawImages[0] : ''

    /*  */
    /* VIDEOS */
    /*  */
    const rawVideos: string[] = []
    const ogVideo = $('meta[property="og:video"]')?.prop('content')
    if (ogVideo) {
      console.log({ ogVideo })
      rawVideos.push(resolveAbsoluteUrl(sourceUrl, ogVideo))
    }
    $('video').map((i: number, ele: any) => {
      const src = $(ele).prop('src')
      if (src) {
        rawVideos.push(resolveAbsoluteUrl(sourceUrl, src))
      }
    })
    const videoObject = JSON.parse($('#videoObject').html())
    if (videoObject?.contentUrl) {
      rawVideos.push(videoObject.contentUrl)
    }
    // sort videos by URL length
    rawVideos.sort((a, b) => a.length - b.length)
    const previewVideo = videoObject?.contentUrl || rawVideos[0] || ''
    console.log({ knownType, rawImages, rawVideos })
    const type = knownType || (previewVideo ? 'video' : previewImage ? 'image' : 'iframe')

    console.log({ type })
    return {
      sourceUrl,
      title,
      description,
      type,
      rawVideos,
      previewVideo,
      rawImages,
      previewImage,
      ts: new Date()
    }
  } catch (error) {
    console.error({ error })
    throw error
  }
}
