
import { toRefs, ref, computed, defineComponent } from 'vue'
import updateProfileValue from '@/services/updateProfileValue'
import { auth, storageRef } from '@/firebase.config'
import Spinner from '@/components/shared/Spinner.vue'

export default defineComponent({
  components: {
    Spinner
  },
  props: {
    field: {
      type: String,
      default: ''
    },
    value: String,
    editMode: Boolean,
    borderColor: String
  },
  setup (props) {
    const tryUrl = ref(props.value || '')
    console.log({ tryUrl })
    const inputId = ref(new Date().getTime().toString())

    const fileMode = computed(() => !tryUrl.value || props.value === tryUrl.value)

    const isUploading = ref(false)
    const handleImageFile = async ({ target }) => {
      isUploading.value = true
      try {
        const { files: [file] } = target

        const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${props.field}`)
        const { ref } = await fileRef.put(file)
        const downloadURL = await ref.getDownloadURL()

        target.value = null
        await updateProfileValue(props.field, downloadURL, props.value)
        tryUrl.value = downloadURL
        console.log('Photo updated! 👍')
      } catch ({ code, message }) {
        console.error('handleImageFile Error', { code, message })
      }
      isUploading.value = false
    }

    return {
      ...toRefs(props),
      updateProfileValue,
      tryUrl,
      fileMode,
      isUploading,
      handleImageFile,
      inputId
    }
  }
})
