<template>
  <div v-bind:style="`background-color: ${cardBgColor ? cardBgColor:'transparent'};`" v-bind:class="`${isCardPreview ? 'card col-6 col-sm-3 m-0 p-0' : 'card col-12 m-0 p-0'} ${editMode || showPublicOptions ? '' : 'border-0' }`">

    <!-- PRIVATE OPTIONS -->
    <div v-if="editMode" class="card-header p-0 m-0 bg-secondary text-center">

        <i class="fa fa-trash text-white mx-2 mx-sm-3" style="cursor: pointer;" @click="onDelete"/>
        <i v-if="hasMorePreviews" class="fa fa-refresh text-white mx-2 mx-sm-3" style="cursor: pointer;" @click="changePreview"/>
        <i class="fa fa-external-link text-white mx-2 mx-sm-3" style="cursor: pointer;" @click="toSource" />
        <i v-if="isCardPreview" class="fa fa-eye text-white mx-2 mx-sm-3" style="cursor: pointer;" @click="toPost" />
    </div>

    <!-- IFRAME -->
    <iframe v-bind:class="isCardPreview ? 'card-content':'post-content'" v-if="contentCard.type === 'iframe'" :src="contentCard.iFrameUrl"
      allowtransparency="true"
      frameborder="0"
      allow="accelerometer; encrypted-media; gyroscope; picture-in-picture"
      allowfullscreen
    />

    <!-- IMAGE -->
    <img v-if="contentCard.type === 'image'" v-bind:class="isCardPreview ? 'card-content':'post-content'" :src="contentCard.previewImage" style="cursor: pointer;" @click="handleImgClick" />

    <!-- VIDEO -->
    <video v-if="contentCard.type === 'video'" v-bind:class="isCardPreview ? 'card-content':'post-content'" :src="contentCard.previewVideo" controls />

    <!-- PUBLIC OPTIONS -->
    <div v-if="showPublicOptions || (!editMode && !isCardPreview)" class="card-footer p-0 m-0 bg-secondary text-center">
      <i class="fa fa-external-link text-white mx-2 mx-sm-3" style="cursor: pointer;" @click="toSource" />
      <i v-if="isCardPreview" class="fa fa-eye text-white mx-2 mx-sm-3" style="cursor: pointer;" @click="toPost" />
    </div>
  </div>
</template>

<script lang="ts">
import { ref, defineComponent } from 'vue'
// import { BaseContentCard } from '@/types/BaseContentCard'
import updateCardValue from '@/services/updateCardValue'
import router from '@/router'

export default defineComponent({
  props: {
    editMode: Boolean,
    userUid: String,
    postUid: {
      type: String,
      default: ''
    },
    cardBgColor: String,
    contentCard: Object,
    onDelete: Function,
    isCardPreview: {
      type: Boolean,
      default: true
    },
    showPublicOptions: {
      type: Boolean,
      default: false
    }
  },
  setup (props) {
    const contentOptions = [
      ...props.contentCard?.rawImages?.map(img => ({
        type: 'image',
        previewImage: img
      })) || [],
      ...props.contentCard?.rawVideos?.map(vid => ({
        type: 'video',
        previewVideo: vid
      })) || []
    ]
    if (props.contentCard?.iFrameUrl) {
      contentOptions.push({
        type: 'iframe'
      })
    }

    let optIndex = 0
    const hasMorePreviews = ref(contentOptions.length > 1)
    const changePreview = async () => {
      optIndex += 1
      if (optIndex > contentOptions.length - 1) {
        optIndex = 0
      }
      const { type, previewImage, previewVideo } = contentOptions[optIndex]

      await Promise.all([
        updateCardValue(props.postUid, 'type', type),
        updateCardValue(
          props.postUid,
          type === 'image' ? 'previewImage' : 'previewVideo',
          type === 'image' ? previewImage : previewVideo,
          type === 'image' ? props.contentCard?.previewImage : props.contentCard?.previewVideo
        )
      ])
    }

    const postUrl = `/u/${props.userUid}/p/${props.postUid}`
    const handleImgClick = () => { props.isCardPreview ? router.push(postUrl) : location.href = props.contentCard?.sourceUrl }
    const toPost = () => { router.push(postUrl) }
    const toSource = () => { location.href = props.contentCard?.sourceUrl }
    return {
      changePreview,
      hasMorePreviews,
      handleImgClick,
      toPost,
      toSource
    }
  }
})
</script>

<style lang="css">
/* Custom CSS here */

.card-content {
  height: 200px;
  /* width: 200px; */
  object-fit: cover;
}
</style>
