<template>
    <div class="container-fluid text-center mx-0 px-0">
        <div v-if="editMode" class="text-center col-12">
          <div v-if="!isUploading">

            <button type="button" class="btn bg-secondary border text-white m-2"><label :for="inputId" style="cursor: pointer;">📸 Upload photo</label></button><input type="file" :id="inputId" @change="handleImageFile" hidden />
            <input type="text" class="m-2 p-1 col-10 col-sm-8" placeholder="Paste image URL" v-model="tryUrl" />
            <button v-if="!fileMode" @click="updateProfileValue(field, tryUrl, value)" type="button" class="btn bg-secondary border text-white m-2"><label style="cursor: pointer;">🌐 Fetch URL</label></button>

          </div>
          <div v-else>
            <Spinner />
          </div>
        </div>
        <img :src="value" class="img-fluid col-12 mx-0 px-0" v-bind:style="borderColor ? `border: solid 2px; ${borderColor};`:''" />
    </div>
</template>

<script lang="ts">
import { toRefs, ref, computed, defineComponent } from 'vue'
import updateProfileValue from '@/services/updateProfileValue'
import { auth, storageRef } from '@/firebase.config'
import Spinner from '@/components/shared/Spinner.vue'

export default defineComponent({
  components: {
    Spinner
  },
  props: {
    field: {
      type: String,
      default: ''
    },
    value: String,
    editMode: Boolean,
    borderColor: String
  },
  setup (props) {
    const tryUrl = ref(props.value || '')
    console.log({ tryUrl })
    const inputId = ref(new Date().getTime().toString())

    const fileMode = computed(() => !tryUrl.value || props.value === tryUrl.value)

    const isUploading = ref(false)
    const handleImageFile = async ({ target }) => {
      isUploading.value = true
      try {
        const { files: [file] } = target

        const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${props.field}`)
        const { ref } = await fileRef.put(file)
        const downloadURL = await ref.getDownloadURL()

        target.value = null
        await updateProfileValue(props.field, downloadURL, props.value)
        tryUrl.value = downloadURL
        console.log('Photo updated! 👍')
      } catch ({ code, message }) {
        console.error('handleImageFile Error', { code, message })
      }
      isUploading.value = false
    }

    return {
      ...toRefs(props),
      updateProfileValue,
      tryUrl,
      fileMode,
      isUploading,
      handleImageFile,
      inputId
    }
  }
})
</script>
